@keyframes landing-bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translate3d(0,0,0); }
    40% {
        transform: translate3d(0,-20px,0); }
    60% {
        transform: translate3d(0,-10px,0); } }
@keyframes landing-bounce2 {
    0%, 15%, 45%, 75%, 100% {
        transform: translate3d(0,0,0); }
    50% {
        transform: translate3d(0,-20px,0); }
    60% {
        transform: translate3d(0,-10px,0); } }

@media (max-height: 1200px) {
    @keyframes landing-bounce {
        0%, 20%, 50%, 80%, 100% {
            transform: translate3d(0,0,0); }
        40% {
            transform: translate3d(0,-10px,0); }
        60% {
            transform: translate3d(0,-5px,0); } }
    @keyframes landing-bounce2 {
        0%, 15%, 45%, 75%, 100% {
            transform: translate3d(0,0,0); }
        50% {
            transform: translate3d(0,-10px,0); }
        60% {
            transform: translate3d(0,-5px,0); } } }

